<template>
    <div>
        <div class="box">
            <h2>日期选择</h2>
            <span class="demonstration">默认</span>
            <el-date-picker
                v-model="value1"
                type="date"
                placeholder="选择日期">
            </el-date-picker>
        </div>
        <div class="box">
            <span class="demonstration">带快捷选项</span>
            <el-date-picker
                v-model="value2"
                align="right"
                type="date"
                placeholder="选择日期"
                :picker-options="pickerOptions1">
            </el-date-picker>
        </div>
        <div class="box">
            <h2>日期时间选择</h2>
            <span class="demonstration">默认</span>
            <el-date-picker
                v-model="value3"
                type="datetime"
                placeholder="选择日期时间">
            </el-date-picker>
        </div>
    </div>
</template>
<script>
export default {
    data () {
        return {
            pickerOptions1: {
                disabledDate (time) {
                    return time.getTime() > Date.now()
                },
                shortcuts: [{
                    text: '今天',
                    onClick (picker) {
                        picker.$emit('pick', new Date())
                    }
                }, {
                    text: '昨天',
                    onClick (picker) {
                        const date = new Date()
                        date.setTime(date.getTime() - 3600 * 1000 * 24)
                        picker.$emit('pick', date)
                    }
                }, {
                    text: '一周前',
                    onClick (picker) {
                        const date = new Date()
                        date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
                        picker.$emit('pick', date)
                    }
                }]
            },
            value1: '',
            value2: '',
            value3: ''
        }
    }
}
</script>
<style>
.el-row {
    margin-bottom: 20px;
    &:last-child {
        margin-bottom: 0;
    }
}
.el-col {
    border-radius: 4px;
}
.el-picker-panel {
    color: #333;
}
.el-date-table td.today span {
    color: #00C4C0;
    font-weight: 700;
}
.el-date-table td.current:not(.disabled) span {
    color: #fff;
    background-color: #00C4C0;
}
.el-picker-panel__icon-btn:hover {
    color: #00C4C0;
}
.el-date-picker__header-label.active,
.el-date-picker__header-label:hover {
    color: #00C4C0;
}
.el-picker-panel__shortcut:hover {
    color: #00C4C0;
}
.el-date-table td.available:hover {
    color: #00C4C0;
}
.el-date-table td span {
    border-radius: 2px;
}
.el-date-table td.today span {
    color: #333;
}
.el-date-table td.available:hover span {
    background: #80e2e0;
    color: #444;
}
.box {
    margin-bottom: 20px;
}
.el-date-picker {
    width: 260px;
}
.el-date-picker .el-picker-panel__content {
    width: 260px;
    margin: 0px;
}
.el-date-picker table {
    width: 250px;
    margin: 0 5px;
}
.el-date-table th {
    padding: 2px;
}
.el-date-picker.has-sidebar {
    width: 370px;
}
</style>



